import React from "react";
import "./Products.scss";

import { div, Link } from "react-router-dom";

//Images
import SphereHeader from "../../assets/img/SphereHeader.jpg";
import SphereHeaderP from "../../assets/img/SphereHeaderP.jpg";
import SphereHeaderQ from "../../assets/img/SphereHeaderQ.jpg";
import EyeHeader from "../../assets/img/EyeHeader.jpg";
import EyeHeaderP from "../../assets/img/EyeHeaderP.jpg";
import UnderNutHeader from "../../assets/img/UnderNutHeader.jpg";

import { motion } from "framer-motion";

const Products = () => {
  return (
    <div id="products">
      <div className="d-flex justify-content-center my-5">
        <h2 className="col-lg-6 col-xm-12 header text-center">Cilindri Hidraulici Telescopici</h2>
      </div>

      <div className="container text-wrapper">
        <p>
          ntreaga gam de Cilindri Telescopici Hidraulici, din stocul nostru,
          este realizat cu materiale de calitate superioar i cu tehnici de
          construcie de ultim generaie care permit performane i
          durabilitate mai mari.
        </p>

        <p>
          Se folosesc evi de oel laminate la cald fr sudur care garanteaz
          o rezisten ridicat la traciune R = N / mm 590 i o limit de
          curgere Rs = N / mm 460. Ultima extensie, n schimb, este din laminat
          C45. Capacul i fundul sunt realizate cu tanare la cald n ASTM
          A105m / 97.
        </p>

        <p>
          Sistemul de prelucrare a tubului utilizeaz un proces care permite
          obinerea opritorului mecanic din tubul brut, funcionnd att din
          exterior, ct i din interior.
        </p>

        <p>
          Opritorul mecanic are avantajul de a avea o suprafa de sprijin mai
          larg i mai solid, permind blocarea extensiilor cnd sunt complet
          deschise.
        </p>
      </div>
      <div className="container products-wrapper text-center">
        <div className="row">
          <motion.div
            whileHover={{
              scale: 1.1,
              originX: 0,
              textShadow: "0px 0px 8px rgb(255,255,255)",
              boxShadow: "0px 0px 8px rgb(255,255,255)",
            }}
            className="col img-wrapper text-center d-flex justify-content-center"
          >
            <img className="image" src={SphereHeader}></img>
            <Link to="/sphereCylinders" className="overflow">
              <h3 className="hover-text text-center">Cilindri cu Sfer</h3>
            </Link>
          </motion.div>
          <motion.div
            whileHover={{
              scale: 1.1,
              originX: 0,
              textShadow: "0px 0px 8px rgb(255,255,255)",
              boxShadow: "0px 0px 8px rgb(255,255,255)",
            }}
            className="col img-wrapper text-center d-flex justify-content-center"
          >
            <img className="image" src={SphereHeaderP}></img>
            <Link to="/sphereCylindersSeriesP" className="overflow">
              <h3 className="hover-text text-center">
                Cilindri cu Sfer Serie P
              </h3>
            </Link>
          </motion.div>
          <motion.div
            whileHover={{
              scale: 1.1,
              originX: 0,
              textShadow: "0px 0px 8px rgb(255,255,255)",
              boxShadow: "0px 0px 8px rgb(255,255,255)",
            }}
            className="col img-wrapper text-center d-flex justify-content-center"
          >
            <img className="image" src={SphereHeaderQ}></img>
            <Link to="/sphereCylindersSeriesQ" className="overflow">
              <h3 className="hover-text text-center">
                Cilindri cu Sfer Serie Q
              </h3>
            </Link>
          </motion.div>
        </div>

        <div className="row">
          <motion.div
            whileHover={{
              scale: 1.1,
              originX: 0,
              textShadow: "0px 0px 8px rgb(255,255,255)",
              boxShadow: "0px 0px 8px rgb(255,255,255)",
            }}
            className="col img-wrapper text-center d-flex justify-content-center"
          >
            <img className="image" src={EyeHeader}></img>
            <Link to="/eyeCylinders" className="overflow">
              <h3 className="hover-text text-center">Cilindri Cu Ochi</h3>
            </Link>
          </motion.div>
          <motion.div
            whileHover={{
              scale: 1.1,
              originX: 0,
              textShadow: "0px 0px 8px rgb(255,255,255)",
              boxShadow: "0px 0px 8px rgb(255,255,255)",
            }}
            className="col img-wrapper text-center d-flex justify-content-center"
          >
            <img className="image" src={EyeHeaderP}></img>
            <Link to="/eyeCylindersSeriesP" className="overflow">
              <h3 className="hover-text text-center">
                Cilindri Cu Ochi Serie P
              </h3>
            </Link>
          </motion.div>
          <motion.div
            whileHover={{
              scale: 1.1,
              originX: 0,
              textShadow: "0px 0px 8px rgb(255,255,255)",
              boxShadow: "0px 0px 8px rgb(255,255,255)",
            }}
            className="col img-wrapper text-center d-flex justify-content-center "
          >
            <img className="image" src={UnderNutHeader}></img>
            <Link to="/underNutCylinders" className="overflow ">
              <h3 className="hover-text text-center">Cilindri Sub Ben</h3>
            </Link>
          </motion.div>
        </div>
      </div>
    </div>
  );
};

export default Products;
